<template>
  <div class="com-container">
    <div class="com-chart" ref="track_map_ref">test</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      chartInstance: null,
      bmap: null,
      deviceIds: null,
      iotMessages: new Map(),
      messageNum: 20,
      centerX: 0.0,
      centerY: 0.0,
      timerId: null,
      isShowTack: false, // 
    };
  },
  mounted() {
    this.initChart();
    // this.getData()
    this.getDataInternal();
    window.addEventListener("resize", this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    clearInterval(this.timerId);
    window.removeEventListener("resize", this.screenAdapter);
  },
  methods: {
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.track_map_ref);

      const initOption = {
        bmap: {
          zoom: 5, // 
          roam: true, // 
        },
      };
      this.chartInstance.setOption(initOption);
      // 
      this.chartInstance.on("click", async (device) => {
        console.log(device.name);
        // 
        this.isShowTack = !this.isShowTack;
        // this.resetEcharts()
        if (this.isShowTack) {
          this.drawTrackInternal(device.name);
        } else {
          this.getDataInternal();
        }
      });

      this.bmap = this.chartInstance.getModel().getComponent("bmap").getBMap();
      // console.log(this.bmap)
      // this.bmap.setMinZoom(6) // 
      // this.bmap.setMaxZoom(12) // 
      this.bmap.addControl(
        new BMap.ScaleControl({ anchor: BMAP_ANCHOR_BOTTOM_LEFT })
      ); // 
      // this.bmap.setMapStyle({style: 'midnight'}) // bmap
      this.bmap.setMapStyleV2({ styleId: "8394c7f10d2cb459727e8c39ba0a3650" });
      // console.log(BMap)
    },
    // 
    getDataInternal() {
      // 
      clearInterval(this.timerId);
      // 1
      this.timerId = setInterval(() => {
        this.getData();
      }, 1000);
    },
    async getData() {
      // device ids
      var { data: ret } = await this.$http.get("device-ids");
      this.deviceIds = ret;
      // console.log(this.deviceIds)
      // ()num1
      for (var i = 0; i < this.deviceIds.length; i++) {
        var { data: msgs } = await this.$http.get("messages-by-id-num", {
          params: {
            id: this.deviceIds[i],
            num: this.messageNum,
          },
        });
        this.iotMessages.set(this.deviceIds[i], msgs);
      }
      // console.log(this.iotMessages)
      this.updateChart();
    },
    updateChart() {
      // seriesArr
      // 
      var locations = new Array();
      this.centerX = 0;
      this.centerY = 0;
      for (var i = 0; i < this.deviceIds.length; i++) {
        var msg = this.iotMessages.get(this.deviceIds[i])[0];
        if (msg == null) {
          return;
        }
        // console.log(msg)
        locations.push({
          name: this.deviceIds[i],
          value: [msg.lng, msg.lat, msg.alert],
        });
        this.centerX += msg.lng;
        this.centerY += msg.lat;
      }
      this.centerX = this.centerX / this.deviceIds.length;
      this.centerY = this.centerY / this.deviceIds.length;
      const dataOption = {
        geo: {
          map: "bmap",
          itemStyle: {},
        },
        bmap: {
          center: [this.centerX, this.centerY],
          zoom: 11,
          roam: true,
        },
        series: [
          {
            name: "",
            type: "effectScatter",
            coordinateSystem: "bmap",
            animation: true, // false
            symbol: "circle",
            symbolSize: 20,
            itemStyle: {
              normal: {
                color: function (point) {
                  // console.log(point.value)
                  if (point.value[2] == 0) {
                    return "#7EF94C";
                  } else if (point.value[2] == 1) {
                    return "#B34038";
                  }
                },
              },
            },
            rippleEffect: {
              period: 4,
              scale: 2.5,
              brushType: "fill",
            },
            data: locations,
            label: {
              normal: {
                formatter: "{b}",
                position: "top",
                show: true,
              },
              emphasis: {
                show: true,
              },
            },
          },
          // echarts4.9.0chart.setOption(option, {replaceMerge: string})
          // series
          {
            name: "null",
            type: "lines",
          },
        ],
      };
      // console.log('locations:')
      // console.log(this.chartInstance.getOption())
      // this.chartInstance.setOption(dataOption, {replaceMerge: ['series', 'dataRange', 'geo']})
      this.chartInstance.setOption(dataOption);
      // console.log(this.chartInstance.getOption())
    },
    screenAdapter() {
      const adapterOption = {};
      this.chartInstance.setOption(adapterOption);
      this.chartInstance.resize();
    },
    drawTrackInternal(deviceId) {
      // 
      clearInterval(this.timerId);
      // deviceId
      this.timerId = setInterval(() => {
        this.drawTrack(deviceId);
      }, 1000);
    },
    // (messageNum)
    async drawTrack(deviceId) {
      var { data: msg } = await this.$http.get("messages-by-id-num", {
        params: {
          id: deviceId,
          num: this.messageNum,
        },
      });
      // console.log(msg)
      // msg
      // 
      msg.reverse();
      // console.log(msg)
      // 
      var lineData = new Array();
      var pointData = new Array();
      this.centerX = 0;
      this.centerY = 0;
      for (var i = 0; i < msg.length - 1; i++) {
        // from->to
        lineData.push({
          // fromName: msg[i].clientId,
          // toName: msg[i + 1].clientId,
          fromName: i,
          toName: i + 1,
          coords: [
            [msg[i].lng, msg[i].lat],
            [msg[i + 1].lng, msg[i + 1].lat],
          ],
        });
        // 
        if (i == 0) {
          pointData.push({
            name: i,
            value: [msg[i].lng, msg[i].lat, msg[i].alert],
          });
        }
        pointData.push({
          // name
          name: i + 1,
          value: [msg[i + 1].lng, msg[i + 1].lat, msg[i + 1].alert],
        });
        this.centerX += msg[i].lng;
        this.centerY += msg[i].lat;
      }
      this.centerX = this.centerX / (msg.length - 1);
      this.centerY = this.centerY / (msg.length - 1);

      const dataOption = {
        geo: {
          map: "bmap",
          polyline: true,
          progressiveThreshold: 100,
          progressive: 400,
          roam: true,
        },
        bmap: {
          center: [this.centerX, this.centerY],
          zoom: 11,
          roam: true,
        },
        series: [
          {
            //
            name: "",
            type: "lines",
            coordinateSystem: "bmap",
            effect: {
              // 
              symbolSize: 5,
              show: true, // locations
              scaleSize: 2,
              period: 20,
              color: "#FFF",
              shadowBlur: 10,
              constantSpeed: 50,
            },
            lineStyle: {
              // 
              normal: {
                color: new this.$echarts.graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#58B3CC",
                    },
                    {
                      offset: 1,
                      color: "#ffbf31",
                    },
                  ],
                  false
                ),
                width: 2, //
                opacity: 0.3,
                curveness: 0.3,
              },
            },
            data: lineData,
          },
          {
            // 
            name: "",
            type: "effectScatter",
            coordinateSystem: "bmap",
            rippleEffect: {
              period: 5,
              scale: 3,
              brushType: "stroke",
            },
            symbolSize: 10,
            itemStyle: {
              normal: {
                color: function (point) {
                  // console.log(point.value)
                  if (point.value[2] == 0) {
                    return "#7EF94C";
                  } else if (point.value[2] == 1) {
                    return "#B34038";
                  }
                },
              },
            },
            label: {
              normal: {
                formatter: "{b}",
                position: "top",
                show: true,
              },
              emphasis: {
                show: true,
              },
            },
            //
            data: pointData,
          },
        ],
      };
      // console.log('track:')
      // console.log(this.chartInstance.getOption())
      // this.chartInstance.setOption(dataOption, {replaceMerge: ['series', 'dataRange', 'geo']})
      this.chartInstance.setOption(dataOption);
      // console.log(this.chartInstance.getOption())
    },
    // // echarts
    // resetEcharts() {
    //     // console.log(this.chartInstance.getOption())
    //     this.chartInstance.clear()
    //     this.chartInstance.setOption({bmap:{}})

    //     this.bmap = this.chartInstance.getModel().getComponent('bmap').getBMap()
    //     this.bmap.addControl(new BMap.ScaleControl({anchor: BMAP_ANCHOR_BOTTOM_LEFT})) // 
    //     this.bmap.setMapStyleV2({styleId: '8394c7f10d2cb459727e8c39ba0a3650'})
    // }
  },
};
</script>

<style>
</style>


