<template>
  <div class="query" ref="query_ref">
    <el-container>
      <MenuBar></MenuBar>
      <el-header>
        <div
          style="
            text-align: center;
            font-size: 25px;
            color: white;
            font-weight: bold;
          "
        >
          <span></span>
        </div>
        <div style="text-align: center; top: 30px; color: white">
          <span></span>
          <el-input
            placeholder=""
            prefix-icon="el-icon-search"
            style="width: 300px; margin-top: 10px"
            v-model="keyword"
            @keyup.enter.native="search"
          ></el-input>
        </div>
      </el-header>
      <el-main style="margin-top: 30px">
        <!--  -->
        <el-card style="width: 90%; padding: 3px 0; margin: auto; opacity: 0.7">
          <el-table
            :data="pageData"
            style="width: 90%; margin: auto"
            :height="tableHeight"
            stripe
            @sort-change="sortByColumn"
            class="table"
          >
            <el-table-column
              type="index"
              align="center"
              :index="getIndex"
            ></el-table-column>
            <el-table-column
              prop="clientId"
              label="ID"
              width="120"
              align="center"
              sortable="custom"
            >
            </el-table-column>
            <el-table-column
              prop="info"
              label=""
              width="300"
              align="center"
              sortable
            >
            </el-table-column>
            <el-table-column
              prop="value"
              label=""
              width="120"
              align="center"
              sortable
            >
            </el-table-column>
            <el-table-column
              prop="alert"
              label=""
              width="120"
              align="center"
              sortable
            >
            </el-table-column>
            <el-table-column
              prop="lng"
              label=""
              width="120"
              align="center"
              sortable
            >
            </el-table-column>
            <el-table-column
              prop="lat"
              label=""
              width="120"
              align="center"
              sortable
            >
            </el-table-column>
            <el-table-column
              prop="timestamp"
              label=""
              width="150"
              align="center"
              sortable
            >
            </el-table-column>
          </el-table>
        </el-card>
      </el-main>
      <el-footer style="margin: auto">
        <el-card style="opacity: 0.8">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
            style="background-color: white"
          >
          </el-pagination>
        </el-card>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import MenuBar from "@/components/MenuBar";
export default {
  data() {
    return {
      tableData: null,
      pageData: null,
      originData: null,
      currentPage: 1,
      pageSize: 10,
      total: null,
      keyword: null,
      tableHeight: null,
    };
  },
  mounted() {
    this.getData();
    window.addEventListener("resize", this.screenAdapter); // 
    this.screenAdapter(); // 
  },
  methods: {
    init() {},
    async getData() {
      var { data: ret } = await this.$http.get("data-by-keyword", {
        params: {
          keyword: this.keyword,
        },
      });
      this.tableData = ret;
      this.originData = this.tableData;
      this.total = ret.length;
      this.flushPageData();
    },
    flushPageData() {
      var start = (this.currentPage - 1) * this.pageSize;
      var end = this.currentPage * this.pageSize;
      this.pageData = this.tableData.slice(start, end);
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.pageSize = val;
      this.flushPageData();
    },
    handleCurrentChange(val) {
      console.log(`${val}`);
      this.currentPage = val;
      this.flushPageData();
    },
    getIndex(index) {
      return (this.currentPage - 1) * this.pageSize + index + 1;
    },
    // getData
    search() {
      console.log(this.keyword);
      this.getData();
    },
    // 
    sortByColumn(column) {
      console.log(column);
      // 
      this.originData = this.tableData;
      // tableData
      var fieldName = column.prop;
      var type = column.order;
      // typenull
      if (type == null) {
        this.tableData = this.originData;
      }
      // sortintbool
      else {
        this.tableData = this.tableData.sort((a, b) => {
          if (type == "ascending") {
            return a[fieldName] < b[fieldName] ? 1 : -1;
          } else if (type == "descending") {
            return a[fieldName] > b[fieldName] ? 1 : -1;
          }
        });
      }
      this.flushPageData();
    },
    screenAdapter() {
      this.tableHeight = window.innerHeight * 0.6;
      console.log(this.tableHeight);
    },
  },
  components: {
    MenuBar,
  },
};
</script>

<style lang="css">
.query {
  background-image: url("../assets/img/bg.png");
  height: 100%; /* 1. datatableHeightnull 2. divheight100% */
  /* color: #222733; */
}
</style>


