<template>
  <div class="com-container">
    <div class="com-chart" ref="datainfo_ref"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      chartInstance: null, // 
      deviceIds: null, // id
      deviceDataNums: null, // 
      currentPage: 1, // 
      totalPage: 0, // 
      numInPage: 3, // 
      timerId: null, // 
      sildeTimerId: null, // id
    };
  },
  // vuemounteddom
  mounted() {
    this.initChart();
    this.getDataInternal();
    window.addEventListener("resize", this.screenAdapter);
    // 
    this.screenAdapter();
  },
  // 
  destroyed() {
    // 
    clearInterval(this.sildeTimerId);
    clearInterval(this.timerId);
    // 
    window.removeEventListener("resize", this.screenAdapter);
  },
  methods: {
    // echart instance
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.datainfo_ref, "chalk");
      // 
      const initOption = {
        title: {
          text: "",
          left: 5,
          top: 5,
        },
        grid: {
          top: "20%",
          left: "3%",
          right: "6%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "category",
          // 
        },
        yAxis: {
          type: "value",
        },
        // 
        tooltip: {
          // 
          trigger: "axis",
          axisPointer: {
            type: "line",
            z: 0, // 
            lineStyle: {
              color: "#2D3443",
            },
          },
        },
        series: [
          {
            // 
            type: "bar",
            // 
            label: {
              show: true,
              position: "top",
              textStyle: {
                color: "white",
              },
            },
            // 
            itemStyle: {
              // +
              // (0, 0)->(0, 1)
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                // 0
                {
                  offset: 0,
                  color: "#5052EE",
                },
                // 100
                {
                  offset: 1,
                  color: "#AB6EE5",
                },
              ]),
            },
          },
        ],
      };
      this.chartInstance.setOption(initOption);

      // 
      // 
      this.chartInstance.on("mouseover", () => {
        clearInterval(this.sildeTimerId);
      });
      // 
      this.chartInstance.on("mouseout", () => {
        this.sildeInterval();
      });
      // 
      this.sildeInterval();
    },
    getDataInternal() {
      this.timerId = setInterval(() => {
        this.getData();
      }, 1000);
    },
    // 
    async getData() {
      // device ids
      var { data: ids } = await this.$http.get("device-ids");
      this.deviceIds = ids;
      this.deviceIds.sort((a, b) => {
        return a < b;
      });
      // device
      this.deviceDataNums = new Array();
      for (var i = 0; i < this.deviceIds.length; i++) {
        var { data: num } = await this.$http.get("message-num-by-id", {
          params: {
            id: this.deviceIds[i],
          },
        });
        this.deviceDataNums.push(num);
      }

      // 
      this.totalPage =
        this.deviceIds.length % this.numInPage === 0
          ? this.deviceIds.length / this.numInPage
          : this.deviceIds.length / this.numInPage + 1;
      // 
      this.updateChart();
    },
    // 
    updateChart() {
      var start = (this.currentPage - 1) * this.numInPage;
      var end = this.currentPage * this.numInPage;
      var showIds = this.deviceIds.slice(start, end);
      var showDatas = this.deviceDataNums.slice(start, end);
      // updateoptiondata
      const dataOption = {
        xAxis: {
          data: showIds,
        },
        series: [
          {
            data: showDatas, // 
          },
        ],
      };
      // option
      this.chartInstance.setOption(dataOption);
    },
    // 
    sildeInterval() {
      // 
      if (this.sildeTimerId) {
        clearInterval(this.sildeTimerId);
      }
      // 3s
      this.sildeTimerId = setInterval(() => {
        this.currentPage++;
        if (this.currentPage > this.totalPage) {
          this.currentPage = 1;
        }
      }, 3000);
    },
    // 
    screenAdapter() {
      // console.log(this.$refs.datainfo_ref.offsetWidth)
      // 
      const titleFontSize = (this.$refs.datainfo_ref.offsetWidth / 100) * 3.6;
      // : option
      const adapterOption = {
        // 
        title: {
          textStyle: {
            fontSize: titleFontSize,
          },
        },
        // 
        tooltip: {
          axisPointer: {
            lineStyle: {
              width: titleFontSize,
            },
          },
        },
        // 
        series: [
          {
            barWidth: titleFontSize,
            // 
            itemStyle: {
              barBorderRadius: [titleFontSize / 2, titleFontSize / 2, 0, 0], // 
            },
          },
        ],
      };
      this.chartInstance.setOption(adapterOption);
      // chartresize
      this.chartInstance.resize();
    },
  },
};
</script>

<style>
</style>


